@import url(https://fonts.googleapis.com/css?family=Odibee+Sans|Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

* {
  box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden; }

html {
  font-size: 125%; }

body {
  background: linear-gradient(-70deg, #6190E8, #FFFFFF, #FFFFFF, #A7BFE8);
  background-size: 400% 400%;
  -webkit-animation: gradientBG 15s ease-in-out infinite;
          animation: gradientBG 15s ease-in-out infinite; }

@-webkit-keyframes gradientBG {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@keyframes gradientBG {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

h1, h3, p {
  text-transform: capitalize; }

h2, h3 {
  text-align: center; }

p {
  margin-bottom: 20px;
  font-size: 1rem;
  font-family: 'Oswald', sans-serif; }

h1 span {
  text-transform: uppercase; }

ul {
  padding: 0;
  margin: 0; }

ul li {
  list-style-type: none; }

.wrapper {
  max-width: 1280px;
  width: 80%;
  margin: 0 auto; }

header {
  background: #6190E8;
  background: -webkit-gradient(linear, left top, right top, from(#A7BFE8), to(#6190E8));
  background: linear-gradient(to right, #A7BFE8, #6190E8);
  padding: 20px 0;
  border: 1px solid #000000; }
  header .wrapper {
    display: flex;
    align-items: center; }
    header .wrapper h1 {
      margin: 0;
      flex: 1 1 auto; }

main h2 {
  font-size: 2rem;
  font-family: 'Odibee Sans', cursive;
  margin: 0 0 50px;
  padding-top: 20px; }

main h3 {
  font-size: 1.6rem;
  font-family: 'Odibee Sans', cursive;
  text-transform: uppercase;
  margin: 0;
  padding: 5px 0;
  background: #6190E8;
  background: -webkit-gradient(linear, left top, right top, from(#A7BFE8), to(#6190E8));
  background: linear-gradient(to right, #A7BFE8, #6190E8);
  border: 1px solid #000000;
  border-bottom: 0;
  letter-spacing: 5px; }

main ul {
  display: flex;
  justify-content: space-around;
  border: 1px solid #000000;
  background: #FFFFFF; }
  main ul li {
    border: 1px solid #000000;
    width: 100%;
    text-align: center;
    padding-bottom: 50px; }
  main ul p {
    margin: 0; }
    main ul p:nth-child(1) {
      font-size: 1.5rem; }
    main ul p:nth-child(2) {
      margin-top: 5px;
      font-size: 1.3rem;
      opacity: 0.7; }

main .timeSeriesButtons {
  display: flex;
  justify-content: space-around;
  padding: 20px 0; }
  main .timeSeriesButtons button {
    font-size: 1.6rem;
    font-family: 'Oswald', sans-serif;
    background: none;
    border: none;
    text-transform: capitalize; }
    main .timeSeriesButtons button:hover {
      color: #6190E8; }

.preloader div {
  display: block;
  position: absolute;
  width: 34px;
  height: 34px;
  margin-top: 10px;
  margin-left: 120px;
  border: 8px solid lightgreen;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: lightgreen transparent transparent transparent; }

.preloader div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s; }

.preloader div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s; }

.preloader div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s; }

@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.flickity-enabled {
  position: relative; }

.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%; }

.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%; }

.flickity-prev-next-button {
  display: none; }

.flickity-page-dots {
  display: none; }

footer {
  text-align: center; }
  footer .tickerCarousel {
    margin-top: 100px;
    background: #000000; }
  footer .tickerCell {
    width: 10%;
    color: #FFFFFF; }
  footer .wrapper p {
    margin: 0; }

